
.api-button-container {
   margin: 70px;
   animation: api-buttons-entry 1.2s ease-out forwards;
}

.api-button {
   width: 130px;
   height: 70px;
   margin-bottom: 40px;
   display: flex;
   justify-content: center;
   align-items: center;
   border: 1px solid #333333;
   border-radius: 15px;
   color: white;
   font-family: 'Roboto';
   font-size: 1.6em;
   letter-spacing: 2px;
   background-color: #0d0d0d;
   box-shadow: 5px 6px 8px 1px rgba(40,40,40,0.8);
}

.api-button:hover {
   cursor: pointer;
   color: #f2a20d;
   border: 0.5px solid #f1f1f2;
   box-shadow: 5px 6px 8px 1px rgb(40, 44, 88, 0.8);
}

@keyframes api-buttons-entry {
   0% {
      margin: 150px;
      padding-bottom: 60px;
      opacity: 0;
   }
   70% {
      opacity: 0.7;
   }
   100% {
      margin: 70px;
      opacity: 1;
   }
}
