@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@600&display=swap);
* {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#icon {
   font-size: 250%;
   width: 50px;
   color: #000;
}

.app {
   width: 100%;
   height: 100vh;
   background-color: #c4dfe6;
}

.calculator-container {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   justify-content: center;
   align-items: center;
   display: flex;
   flex-wrap: wrap;
   width: 90%;
   height: 100%;
}

.calculator {
   width: 350px;
   height: 550px;
   margin-left: 5%;
   padding: 5px;
   border: 1px solid #333333;
   border-radius: 15px;
   background-color: #0d0d0d;
   box-shadow: 10px 13px 8px 1px rgba(40,40,40,0.8);
   -webkit-animation: calculator-entry 1.5s ease-out forwards;
           animation: calculator-entry 1.5s ease-out forwards;
}

.row {
   width: 100%;
   display: flex;
   justify-content: center;
}

.input {
   display: flex;
   justify-content: flex-end;
   align-items: center;
   height: 2.5em;
   margin-bottom: 15px;
   padding: 15px;
   font-size: 2.7em;
   font-weight: bold;
   color: #ffffff;
   border-bottom: 1px solid white;
}

@-webkit-keyframes calculator-entry {
   from { opacity: 0; }
   to { opacity: 1; }
}

@keyframes calculator-entry {
   from { opacity: 0; }
   to { opacity: 1; }
}


.button {
   display: flex;
   justify-content: center;
   align-items: center;
   margin: 5px;
   width: 3em;
   height: 3em;
   border-radius: 100%;
   font-size: 1.5em;
   font-weight: 700;
   color: white;
   font-family: 'Poppins';
   letter-spacing: 1px;
   background-color: #4a464b;
   -webkit-user-select: none;
       -ms-user-select: none;
           user-select: none;
}

.button:hover {
   cursor: pointer;
   border: 0.5px solid #f1f1f2;
}

.operator { background-color: #095077; }

.equals { background-color: #282c58; }

.func { background-color: #f2a20d; }

.wider {
   width: 6.2em;
   border-bottom-left-radius: 45% 100%;
   border-bottom-right-radius: 45% 100%;
   border-top-left-radius: 45% 100%;
   border-top-right-radius: 45% 100%;
}

@-webkit-keyframes shadowFadeOut {
   from { box-shadow: 0px 0px 8px 5px rgb(82, 132, 159, 0.8); }
   to { box-shadow: none;}
}

@keyframes shadowFadeOut {
   from { box-shadow: 0px 0px 8px 5px rgb(82, 132, 159, 0.8); }
   to { box-shadow: none;}
}


.api-button-container {
   margin: 70px;
   -webkit-animation: api-buttons-entry 1.2s ease-out forwards;
           animation: api-buttons-entry 1.2s ease-out forwards;
}

.api-button {
   width: 130px;
   height: 70px;
   margin-bottom: 40px;
   display: flex;
   justify-content: center;
   align-items: center;
   border: 1px solid #333333;
   border-radius: 15px;
   color: white;
   font-family: 'Roboto';
   font-size: 1.6em;
   letter-spacing: 2px;
   background-color: #0d0d0d;
   box-shadow: 5px 6px 8px 1px rgba(40,40,40,0.8);
}

.api-button:hover {
   cursor: pointer;
   color: #f2a20d;
   border: 0.5px solid #f1f1f2;
   box-shadow: 5px 6px 8px 1px rgb(40, 44, 88, 0.8);
}

@-webkit-keyframes api-buttons-entry {
   0% {
      margin: 150px;
      padding-bottom: 60px;
      opacity: 0;
   }
   70% {
      opacity: 0.7;
   }
   100% {
      margin: 70px;
      opacity: 1;
   }
}

@keyframes api-buttons-entry {
   0% {
      margin: 150px;
      padding-bottom: 60px;
      opacity: 0;
   }
   70% {
      opacity: 0.7;
   }
   100% {
      margin: 70px;
      opacity: 1;
   }
}

